@import url(//fonts.googleapis.com/css?family=Droid+Sans);
.sidebar {
  width: 2.65in;
  background-color: #1C4587;
  text-align: left;
  height: 100%;
  color: #fff;
  display: inline-block;
  left: 0;
  position: absolute;
}

.sidebar-heading {
  padding: 0.25rem 1rem;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  font-family: 'Droid Sans';
  text-align: center;
  font-weight: 300;
  font-size: 26pt;
  padding-top: 2.4rem;
}

.sidebar-subHeading {
  display: block;
  text-align: center;
  line-height: 1.2rem;
}

.sidebar-sectionHeader {
  background-color: #0A3154;
  padding: 0.25rem 1rem;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

.sidebar-sectionContents {
  padding: 0 1rem;
  font-size: 11pt;
  margin: 0.5rem 0;
  line-height: 1.2rem;
}

.main {
  position: absolute;
  display: inline-block;
  width: 5.85in;
  right: 0;
  font-family: sans-serif;
}

.main-intro {
  font-size: 11pt;
  padding: 1.5rem 1rem;
  font-weight: 500;
}

.main-sectionHeader {
  color: #0A3154;
  text-align: left;
  margin: 0 1.5rem 0 0.5rem;
  padding: 0.5rem 0.5rem;
  border-bottom: 0.1rem solid #0A3154;
  border-top: 0.1rem solid #0A3154;
  font-family: 'Droid Sans';
  font-weight: 700;
}

.main-sectionContainer {
  padding: 1rem 0;
}

.language-name {
  font-size: 11pt;
}

.language-proficiencyContainer {
}

.language-proficiency {
  text-align: right;
  font-size: 8pt;
}

.language-proficiencyBar {
  background-color: #0f0;
  height: 0.5rem;
  margin: 1rem 0 0 0;
}

.language-proficiencyBar--strong {
  background-color: #0f0;
}

.language-proficiencyBar--moderate {
  background-color: #ff0;
}

.language-proficiencyBar--some {
  background-color: #f80;
}

.language-proficiencyBar--novice {
  background-color: #f88;
}

.language-proficiencyBar--none {
  background-color: #000;
}

.experience {
  display: flex;
  padding: 0.5rem 0.75rem;
  text-align: left;
  font-size: 11pt;
}

.experience-dates {
  flex: 1 1;
}

.experience-details {
  flex: 4 1;
}

.experience-title {
  font-size: 12pt;
  font-weight: 700;
}

.experience-location {
  font-style: italic;
  padding-bottom: 0.25rem;
}

.experience-points {
  -webkit-padding-start: 1rem;
          padding-inline-start: 1rem;
  margin-bottom: 0rem;
}

.experience-point {
  padding-left: 1rem;
}

.education {
  padding: 0.5rem 0.75rem;
  text-align: left;
  font-size: 11pt;
}

.education-title {
  font-size: 12pt;
  font-weight: 700;
}

.education-location {
  font-style: italic;
  padding-bottom: 0.25rem;
}

.education-points {
  -webkit-padding-start: 1rem;
          padding-inline-start: 1rem;
  margin-bottom: 0rem;
}

.education-point {
  padding-left: 1rem;
}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  background-color: #555;
  text-align: center;
  height: 13in;
}

.resume {
  width: 8.5in;
  height: 11in;
  background-color: #ffffff;
  display: inline-block;
  margin: 0 auto;
  top: 3.75rem;
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4)
}

.downloadLink {
  display: block;
  position: relative;
  top: 5rem;
}

